<template>
  <nav-bar>
    <span slot="title" class="title">微信管理</span>
  </nav-bar>
  <div class="content-form">
    <form @submit="update" accept-charset="UTF-8">
      <div class="form-group row">
        <label for="inputEmail3" class="col-sm-2 form-control-label">微信公众账号</label>
        <div class="col-sm-10">
          <input class="form-control" placeholder="微信公众账号" type="text" v-model="shop_public_account.public_account" />
        </div>
      </div>
      <div class="form-group row">
        <label for="inputPassword3" class="col-sm-2 form-control-label">AppId</label>
        <div class="col-sm-10">
          <input class="form-control" placeholder="公众号AppId" type="text" v-model="shop_public_account.app_id" />
        </div>
      </div>
      <div class="form-group row">
        <label for="inputEmail3" class="col-sm-2 form-control-label">AppSecret</label>
        <div class="col-sm-10">
          <input class="form-control" placeholder="公众号AppSecret" type="text" v-model="shop_public_account.app_secret" />
        </div>
      </div>
      <div class="form-group row">
        <label class="col-sm-2 form-control-label"></label>
        <div class="col-sm-10">
          <input type="submit" value="绑 定" class="btn btn-primary" />
        </div>
      </div>
    </form>
  </div>
  <table class="table">
    <tr>
      <td colspan="4">绑定后拥有以下资源：</td>
    </tr>
    <tr>
      <td width="110">微信菜牌：</td>
      <td>{{shop_public_account.products_menu_url}}</td>
      <td width="100">
        <div id="qrcode"></div>
      </td>
      <td>
        <p>
          只要用你的微信扫一扫，就能打开你的餐牌页面。
          然后分享到你的朋友圈，就能让关注你的顾客在餐牌页面看到你店铺的菜色咯。
        </p>
      </td>
    </tr>
  </table>
  <!--
  <table class="table">
    <tr>
      <td>绑定后拥有以下功能：</td>
    </tr>
    <tr>
      <td>
        <a v-link="{ path: '/home/weixin/diymenu' }" class="btn btn-success" v-bind:class="{'disabled': !shop_public_account.is_valid}">自定义菜单</a></td>
    </tr>
  </table>
-->
</template>
<script>
  import { showAlert, showLoading, hideLoading } from '../vuex/actions'

  export default {
    vuex: {
      getters: {
        isLogged: state => state.merchant.isLogged,
        access_token: state => state.merchant.merchant.access_token
      },
      actions: {
        showAlert,
        showLoading,
        hideLoading
      }
    },
    data () {
      return {
        shop_public_account: {
          public_account: '',
          app_id: '',
          app_secret: '',
          is_valid: false,
          products_menu_url: ''
        }
      }
    },
    route: {
      data (transition) {
        this.showLoading()
        this.$http.get('http://jiancan.me/api/m1/weixins.json', { access_token: this.access_token }).then(function (response) {
          this.$set('shop_public_account', response.data)
          jQuery('#qrcode').qrcode({width: 80, height: 80, text: this.shop_public_account.products_menu_url})
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    methods: {
      update () {
        this.showLoading()

        var formData = new FormData()
        formData.append('access_token', this.access_token)
        formData.append('shop_public_account[public_account]', this.shop_public_account.public_account)
        formData.append('shop_public_account[app_id]', this.shop_public_account.app_id)
        formData.append('shop_public_account[app_secret]', this.shop_public_account.app_secret)

        this.$http.put('http://jiancan.me/api/m1/weixins/binding.json', formData).then(function (response) {
          this.$set('shop_public_account', response.data)
        }, function (response) {
          this.showAlert(response.data.error)
        })
        this.hideLoading()
      }
    },
    components: {
      'nav-bar': require('../components/nav-bar.vue')
    }
  }
</script>



